@media only screen and (max-width: 768px) {

  .question_wrapper {
    width: calc(330px + (700 - 330) * ((100vw - 360px) / (769 - 360)));
  }

  .question_card_text {
      padding: 10px 15px 0px 15px;
  }

  .question_card_text span {
    text-align: center;
    font-family: 'Lato';
    font-size: calc(14px + (16 - 14) * ((100vw - 360px) / (769 - 360)));
    font-weight: 300;
  }

  .question_card_choices_sub_container {
    flex-direction: column;
  }

  .question_card_choices_sub_wrapper {
    width: 100%;
  }

}