.leader_boards_wrapper {
  background-image: linear-gradient(0deg, rgba(255, 255, 255, 1), rgba(200, 200, 200, 1));
  border-radius: 5px;
  margin: 0px 0px 20px 0px;
  width: calc(700px + (1100 - 700) * ((100vw - 769px) / (1920 - 769)));
  padding: 0px 0px 10px 0px;
  display: flex;
  flex-direction: column;
}