.question_card_choice_button_container {
  border: 0px solid black;
  box-sizing: border-box;
  margin: 5px;
  height: 100%;
}

.question_card_choice_button {
  animation: fade_in .5s ease-in-out 1;
  -webkit-appearance: none;
  background-image: linear-gradient(0deg, rgba(0, 0, 51, 1), rgba(0, 0, 102, 1));
  border: 0px solid black;
  border-radius: 5px;
  box-sizing: border-box;
  color: rgba(235, 235, 235, 1);
  cursor: pointer;
  font: 300 12pt 'PoppinsLight', sans-serif;
  padding: 10px 20px;
  outline: -webkit-focus-ring-color auto 0px;
  text-align: center;
  text-decoration: none;
  width: 100%;
  word-spacing: normal;
}

.question_card_choice_button_active {
  -webkit-appearance: none;
  background-image: linear-gradient(0deg, rgba(211, 143, 57, 1), rgba(244, 166, 66, 1));
  border: 0px solid black;
  border-radius: 5px;
  box-sizing: border-box;
  color: rgba(50, 50, 50, 1);
  cursor: pointer;
  font: 300 12pt 'PoppinsLight', sans-serif;
  padding: 10px 20px;
  outline: -webkit-focus-ring-color auto 0px;
  text-align: center;
  text-decoration: none;
  width: 100%;
  word-spacing: normal;
}

.question_card_choice_button_disabled {
  -webkit-appearance: none;
  background: rgba(174,174,174, 0);
  border: 0px solid black;
  box-sizing: border-box;
  color: rgb(235, 235, 235, 0);
  cursor: default;
  font: 300 12pt 'PoppinsLight', sans-serif;
  opacity: 0;
  outline: -webkit-focus-ring-color auto 0px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  word-spacing: normal;
  width: 100%;
}

.question_card_choice_button:hover,
.question_card_choice_button_active:hover {
  color: rgba(50, 50, 50, 1);
  background-color: rgba(244, 166, 66, 1);
  border: 0px solid black;
  box-sizing: border-box;
  font: 600 12pt 'PoppinsLight', sans-serif;
  padding: 10px 20px;
  transition: background-color .2s ease-in-out 0s;
}

.question_card_choice_button img,
.question_card_choice_button_active img {
  width: 20px;
  height: 20px;
}

.question_card_choice_button span,
.question_card_choice_button_active span {
  align-self: stretch;
  white-space: nowrap;
}

.question_card_choice_button:active span,
.question_card_choice_button_active:active span {
  color: rgba(50, 50, 50, 1);
  display: block;
  opacity: 0;
  font: 600 12pt 'PoppinsLight', sans-serif;
  transform: translate(0px, 15px);
  transition: all .15s ease-in-out 0s;
  transition-property: opacity, transform;
  outline: -webkit-focus-ring-color auto 0px;
  white-space: nowrap;
}