.docs_third_header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  padding: 5px 10px 5px 0px;
  margin-left: -5px;
}

.docs_third_header_left {
  align-self: stretch;
  display: flex;
}

.docs_third_header_left h4 {
  align-self: center;
  display: flex;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0em;
  margin-inline-end: 0em;
  font-family: 'PoppinsLight';
  font-size: calc(14px + (16 - 14) * ((100vw - 769px) / (1920 - 769)));
  font-weight: 300;
}

.docs_third_header_right {
  align-self: stretch;
  display: flex;
  text-align: center;
  justify-content:center;
}

.docs_third_header_right h5 {
  align-self: center;
  display: flex;
  text-align: center;
  font: 300 20pt 'Lato';
}