.default_button_left_disabled {
  -webkit-appearance: none;
  align-items: center;
  align-content: center;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  background: rgba(174,174,174, 0);
  border: 0px solid black;
  border-radius: 5px 0px 0px 5px;
  box-sizing: border-box;
  color: rgb(235, 235, 235, 0);
  cursor: default;
  font: 100 12pt 'PoppinsLight', sans-serif;
  letter-spacing: normal;
  margin: 0px;
  opacity: 0;
  outline: -webkit-focus-ring-color auto 0px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  text-indent: 0px;
  text-rendering: auto;
  text-shadow: none;
  text-transform: none;
  word-spacing: normal;
}

