.nav_bar_button_right {
  -webkit-appearance: none;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(0deg, rgba(0, 0, 51, 1), rgba(0, 0, 102, 1));
  border: 0px solid black;
  border-radius: 0px 5px 0px 0px;
  box-sizing: border-box;
  cursor: pointer;
  margin: 0px;
  padding: 10px 30px;
  outline: -webkit-focus-ring-color auto 0px;
  word-spacing: normal;
}

.nav_bar_button_right_active {
  -webkit-appearance: none;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(0deg, rgba(211, 143, 57, 1), rgba(244, 166, 66, 1));
  border: 0px solid black;
  border-radius: 0px 5px 0px 0px;
  box-sizing: border-box;
  cursor: pointer;
  margin: 0px;
  padding: 10px 30px;
  outline: -webkit-focus-ring-color auto 0px;
  word-spacing: normal;
}

.nav_bar_button_right a,
.nav_bar_button_right_active a {
}

.nav_bar_button_right img,
.nav_bar_button_right_active img {
  width: 30px;
  height: 30px;
}

.nav_bar_button_right span,
.nav_bar_button_right_active span {
  align-self: stretch;
}

.nav_bar_button_right:hover,
.nav_bar_button_right_active:hover {
  background-color: rgba(244, 166, 66, 1);
  border: 0px solid black;
  box-sizing: border-box;
  padding: 10px 30px;
  transition: background-color .2s ease-in-out 0s;
}

.nav_bar_button_right:hover span,
.nav_bar_button_right_active:hover span {
  color: rgba(50, 50, 50, 1);
}

.nav_bar_button_right:active span,
.nav_bar_button_right_active:active span {
  color: rgba(50, 50, 50, 1);
  display: block;
  opacity: 0;
  transform: translate(0px, 15px);
  transition: all .15s ease-in-out 0s;
  transition-property: opacity, transform;
  outline: -webkit-focus-ring-color auto 0px;
}

.nav_bar_button_right:active img,
.nav_bar_button_right_active:active img {
  width: 30px;
  height: 30px;
  opacity: 0;
  transform: translate(0px, 15px);
  transition: all .15s ease-in-out 0s;
  transition-property: opacity, transform;
  outline: -webkit-focus-ring-color auto 0px;
}