.results_achievement_container {
  animation: fade_in .25s ease-in-out 1;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 1), rgba(200, 200, 200, 1), rgba(255, 255, 255, 1));
  margin: 0px 2px 0px 2px;
}

.results_achievement_header_container {
  background-image: linear-gradient(0deg, rgba(211, 143, 57, 1), rgba(244, 166, 66, 1));
  padding: 5px 0px;
  width: 100%;
}

.results_achievement_header_container h3 {
  color: rgba(50, 50, 50, 1);
  font: 600 15pt 'Lato';
  text-align: center;
}

.results_achievement_sub_container {
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 1), rgba(200, 200, 200, 1));
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  padding: 0px 0px;
  width: 100%;
}